/** Module */
import { FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
/** Service */
import { AuthService } from 'src/app/services/authentication/login/auth.service';
import { SessionService } from '../../../services/authentication/session/session.service';
import { TokenService } from 'src/app/services/authentication/token/token.service';
/** Component */
import { DialogComponent } from '../../shared/dialog/dialog.component';
/** Interfaces */
import { Authenticate } from '../../../interfaces/auth';
import { DialogClass } from '../../../utils/dialog-data';
import { UserSessionService } from '../../../services/authentication/user/user-session.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  public nameUserFromControl = new FormControl('', [Validators.required, Validators.minLength(2)]);
  public passwordFromControl = new FormControl('', [Validators.required, Validators.minLength(3)]);
  public hidePass = true;
  public status: boolean;
  /** Properties SnackBar */
  public color = 'primary';
  public mode = 'buffer';
  public value = 0;
  public bufferValue = 0;
  private user: Authenticate;

  constructor(
    private router: Router,
    private auth: AuthService,
    private userSessionService: UserSessionService,
    private tokenService: TokenService,
    public dialog: MatDialog
  ) {
  }

  ngOnInit() {
  }

  onLogin(): void {
    this.status = true;
    this.load('indeterminate', 50, 75);
    if (!this.nameUserFromControl.invalid && !this.passwordFromControl.invalid) {
      this.user = { user: this.nameUserFromControl.value, pass: this.passwordFromControl.value };
      this.auth.login(this.user)
        .then((res) => {
          this.load('buffer', 0, 0);
          this.tokenService.putToken('info token');
          this.userSessionService.putUser('info user');
          this.router.navigate(['/home']);
        })
        .catch((err) => {
          this.openDialog(new DialogClass(err.code, err.message));
        });
    }
  }

  openDialog(dataD: DialogClass) {
    this.dialog.open(DialogComponent, { width: '350px', data: dataD });
    this.load('buffer', 0, 0);
    this.status = false;
  }

  load(mode: string, value: number, buffer: number): void {
    this.mode = mode;
    this.value = value;
    this.bufferValue = buffer;
  }
}


